        <td>템플릿</td>
        <td>내용입력</td>
      </tr>
    </thead>

    <tbody>
      <tr>
        <td>
          <div class="image">
            <canvas id="imgCanvas" width="1186" height="339"></canvas>
          </div>
        </td>

        <td><input type="text" @input="setText"></td>
      </tr>
    </tbody>
  </table>
