<script setup>
const setText = async (val) => {
  await loadImage(val.target.value);
}
</script>

<script>
const fontSize = [150, 130, 110, 97, 84, 74, 67, 60, 56, 52, 48];

import Template2 from '@/assets/template/template_2.png'
export default {
  name: 'CoverComponent',
  props: {},
  mounted() {
    loadImage();
  }
}

function loadImage(_val) {
  const canvas = document.getElementById('imgCanvas');
  const ctx = canvas.getContext('2d');
  const img = new Image();

  img.src = Template2;
  img.onload = () => {
    canvas.width = img.width;
    canvas.height = img.height;
    ctx.drawImage(img, 0, 0);

    if (_val) {
      console.log(_val.length);
      console.log(fontSize);
      if ((_val.length - 5) >= 0) {
        ctx.font = `${fontSize[_val.length - 5]}px mono`;
      } else {
        ctx.font = `${fontSize[0]}px mono`;
      }

      ctx.fillText(_val, 500, 300);
    }
  }

  return ctx;
}
</script>

